.blurred {
  filter: blur(4px);
}

.remove-resizer .react-resizable-handle {
  display: none;
}

.react-resizable-handle-nw {
  background: gainsboro;
  border-radius: 100%;
}

.toolbar-button {
  border-radius: 5px;
  overflow: hidden;
}

.toolbar-button.selected {
  --background: rgba(var(--ion-color-primary-rgb), 0.14);
}

.toolbar-button.selected {
  color: var(--ion-color-primary);
}

.tweet-container {
  transition: all 0.2s ease;
}

.activeImage ion-backdrop::before {
  content: "";
  height: var(--active-image-height);
  z-index: 3;
  top: var(--active-image-top);
  position: absolute;
  background: var(--active-image-url);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  left: var(--active-image-left);
  width: var(--active-image-width);
  border-radius: 16px;
}

.emoji {
  width: 1.2em;
  margin-left: 0.075em;
  margin-right: 0.075em;
  vertical-align: middle;
}
