/* latin */
html {
  background-color: #121212;
}

@font-face {
  font-family: "Vibur";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(./Fonts/vibur.woff2) format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA,
    U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215,
    U+FEFF, U+FFFD;
}
.neon {
  font-family: Vibur;
  color: #fff;
  font-size: 30px;
  padding: 15px 20px;
  text-shadow: 0 0 7px rgba(255, 255, 255, 0), 0 0 10px rgba(255, 255, 255, 0),
    0 0 21px #ffffffd9, 0 0 42px rgba(56, 128, 255, 0.71),
    0 0 82px rgba(56, 128, 255, 0.4), 0 0 92px rgba(56, 128, 255, 0),
    0 0 102px rgba(56, 128, 255, 0), 0 0 151px var(--ion-color-primary);
  display: flex;
}

.popover-mochol {
  --box-shadow: rgb(136 153 166 / 20%) 0px 0px 15px,
    rgb(136 153 166 / 15%) 0px 0px 3px 1px;
}

.action-sheet-destructive .action-sheet-icon {
  --color: var(--ion-color-danger, #eb445a) !important;
}
